/* ============================================= */
/*              Navigation                       */
/* ============================================= */

.nav {
	padding-left: 0;
	flex-basis: 100%;
	display: flex;
	flex-direction: column;

	@media #{$break-medium} {
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
	}

	a {
		text-align: center;
		border-radius: 5px;
		padding: 10px 0;
		display: block;
		width: 100%;
		transition: background-color .2s ease-in-out, color .2s ease-in-out;

		&:hover {
			color: $white;
			background-color: $color-primary;
		}
	}
}
