// ======================================
//  Variables
// ======================================

// Colors
$white: #fff;

$color-primary: #58e1c1; // main light sea green color used on the site
$color-secondary: #51ddfc; // a links and .default background color bright blue
$color-border: #ccc;

// Fonts & Headings
$color-text: #777; // slate grey for text and headings

// Buttons
$color-error: #e4757a;
$color-info: #927bc1;
$color-success: #63cc82;
$color-warning: #fd7856;

// Breakpoint Media Query
$break-medium: '(min-width: 768px)';

// Columns Grid Colors Map
$grid-backgrounds: (
	'1': #927bc1,
	'2': #fd7856,
	'3': #deb2ee,
	'4': #7ee6fd,
	'5': #88d9a0,
	'6': #4da2bb,
	'7': #ec9fa3,
	'8': #fd9f85,
	'9': #e4757a,
	'10':#63cc82,
	'11':#51ddfc,
	'12':#80e8d1
);

// Columns Flex Basis Map Media Queries
$flex-columns: (
	'1': 8.33%,
	'2': 16.67%,
	'3': 25.00%,
	'4': 33.33%,
	'5': 41.67%,
	'6': 50.00%,
	'7': 58.33%,
	'8': 66.67%,
	'9': 75.00%,
	'10': 83.33%,
	'11': 91.67%,
	'12': 100%
);
